<!--第三方iframe-->
<template>
  <div class="outerIframe">
    <iframe class="iframe" src="http://www.baidu.com" frameborder="0"></iframe>
    <div class="back" @click="back">回首页</div>
  </div>
</template>
<script>
export default {
  name: "OuterIframe",
  components: {
  },
  data() {
    return {};
  },
  mounted() {
    
  },
  methods: {
    back(e) {
      this.$router.replace({
        name: "Home"
      });
      e.stopPropagation()
    }
  }
}
</script>
<style lang="less" scoped>
  .outerIframe{
    height: 100%;
    .iframe{
      width: 100%;
      height: 100%;
    }
    .back{
      position: fixed;
      width: 2rem;
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      font-size: 0.4rem;
      bottom: 2rem;
      right: 1rem;
      z-index: 100000;
      background: #f00;
      color: #fff;
    }
  }
</style>
